import React, { Component } from 'react';

class Header extends Component {
  render() {
    return (
      <header id="home">

      <nav id="nav-wrap">

         <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
	      <a className="mobile-btn" href="#home" title="Hide navigation">Hide navigation</a>

         <ul id="nav" className="nav">
            <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
            <li><a className="smoothscroll" href="#about">About</a></li>
	         <li><a className="smoothscroll" href="#resume">Resume</a></li>
            <li><a className="smoothscroll" href="#portfolio">Works</a></li>
            <li><a className="smoothscroll" href="#contact">Contact</a></li>
         </ul>

      </nav>

      <div className="row banner">
         <div className="banner-text">
            <h1 className="responsive-headline">I'm Zach Wilson.</h1>
            <h3>Welcome to my portfolio website! I am a skilled <span>Software Developer</span> based in <span>Buffalo, NY</span>, 
               specializing in Full Stack Web Development. Join me as I showcase my latest projects and share my passion for technology and adventure!</h3>
            <hr />
            <ul className="social">
            <li><a href="https://www.facebook.com/zchbob/"><i className="fa fa-facebook"></i></a></li>
            <li><a href="https://twitter.com/the_ZachWilson_"><i className="fa fa-twitter"></i></a></li>
            <li><a href="https://www.linkedin.com/in/zach-wilson-b202a08b/"><i className="fa fa-linkedin"></i></a></li>
            <li><a href="https://www.instagram.com/the_zachwilson_/"><i className="fa fa-instagram"></i></a></li>
            <li><a href="https://github.com/zachwilson21"><i className="fa fa-github"></i></a></li>
            </ul>
         </div>
      </div>

      <p className="scrolldown">
         <a className="smoothscroll" href="#about"><i className="icon-down-circle"></i></a>
      </p>

   </header>
    );
  }
}

export default Header;



