import React, { Component } from 'react';
import tiko from './../images/tikojarwebsite.jpg';
import music from './../images/MusicExchange.jpg';
import time from './../images/time.jpg';
import file from './../images/file.jpg';

class Portfolio extends Component {
  render() {
    return (
      <section id="portfolio">

        <div className="row">

          <div className="twelve columns collapsed">

            <h1>Check Out Some of My Works.</h1>

            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
              <div className="columns portfolio-item">
                TikoJar Website Project - Built in React
                <a href="https://tikojar.com" title='TikoJar Discord Bot'>
                  <div className="item-wrap">
                    <img alt='tba' src={tiko} />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>TikoJar Discord Bot</h5>
                        <p>Discord Bot</p>
                      </div>
                    </div>
                    <div className="link-icon"><i className="fa fa-link"></i></div>
                  </div>
                </a>
              </div>
              <div className="columns portfolio-item">
                Music Exchange Website - Simple HTML Build
                <a href="https://zachwilson21.github.io/isc325Project/" title='Music Exchange Gallery'>
                  <div className="item-wrap">
                    <img alt='tba' src={music} />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Music Exchange Gallery</h5>
                        <p>School Project</p>
                      </div>
                    </div>
                    <div className="link-icon"><i className="fa fa-link"></i></div>
                  </div>
                </a>
              </div>
              <div className="columns portfolio-item">
                Time Tracking Application - Built in ASP.NET with C#
           
                  <div className="item-wrap">
                    <img alt='tba' src={time} />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Time Tracking Application</h5>
                        <p>Work Project Requires Authentication - Link Removed</p>
                      </div>
                    </div>
                    <div className="link-icon"><i className="fa fa-link"></i></div>
                  </div>
             
              </div>
              <div className="columns portfolio-item">
              Client File Manager - Built in ASP.NET with C#
     
                  <div className="item-wrap">
                    <img alt='tba' src={file} />
                    <div className="overlay">
                      <div className="portfolio-item-meta">
                        <h5>Client File Manager</h5>
                        <p>Work Project Requires Authentication - Link Removed</p>
                      </div>
                    </div>
                    <div className="link-icon"><i className="fa fa-link"></i></div>
                  </div>
            
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Portfolio;



