import React, { Component } from 'react';
import pfp from './../images/profilepic.JPG';

class About extends Component {
  render() {
    return (
      <section id="about">
      <div className="row">
         <div className="three columns">
            <img className="profile-pic"  src={pfp} alt="Zach Wilson Profile Pic" />
         </div>
         <div className="nine columns main-col">
            <h2>About Me</h2>

            <p>I am a passionate Full Stack Developer with a well-rounded skillset that includes modern design methods 
               and proficiency in Web Development technology. I'm always eager to learn 
               new techs and leverage my expertise to deliver high-quality, efficient solutions that drive business 
               success. With my current position at Belvista Software, I'm fortunate to pursue my passion for innovation 
               and explore cutting-edge technologies. Beyond my work, I enjoy exploring the world with my son, from hitting 
               the slopes to discovering new travel destinations and challenging ourselves to score jackpots at the latest arcades. 
               Welcome to my portfolio website, where I invite you to explore my latest projects and share my love for adventure 
               and technology.</p>
            <div className="row">
               <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
						   <span>Zach Wilson</span><br />
						   <span>
						         Rochester, NY
                   </span><br />
						   <span>585-519-8701</span><br />
                     <span>zachjwilson1@gmail.com</span>
					   </p>
               </div>
               <div className="columns download">
                  <p>
                     <a href="https://zachwilson21.github.io/344-Work-Website/PDF/Zach%20Wilson%20Resume.pdf" target="_blank" rel="noopener noreferrer" className="button"><i className="fa fa-download"></i>Download Resume</a>
                  </p>
               </div>
            </div>
         </div>
      </div>

   </section>
    );
  }
}

export default About;



